@import 'styles/base';

:host {
  position: fixed;
  width: 100%;
  bottom: 65px;

  display: inline-flex;
  // justify-content: center;
  align-items: center;

  z-index: 1002;

  flex-direction: column;
  flex-wrap: nowrap;

  gap: 15px;

  pointer-events: none;

  .notifier {
    box-sizing: border-box;
    position: relative;

    pointer-events: auto;

    max-width: 625px;
    width: 625px;

    border-radius: 25px;
    overflow: hidden;
    background-size: 100% 100%;

    &.error {
      background-image: url('/assets/images/backgrounds/notification-background.png'), linear-gradient(92deg, #BE2517 -0.27%, #EB3E2E 70.39%);
      box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.34);

      .notifier-navigation {
        path {
          stroke: #EB3E2E;
        }
      }
    }

    &.success {
      background-image: url('/assets/images/backgrounds/notification-background.png'), linear-gradient(92deg, #008300 -0.27%, #00AF00 70.39%);
      box-shadow: 0 0 32px 0 rgba(1, 175, 1, 0.29);

      .notifier-navigation {
        path {
          stroke: #00AF00;
        }
      }
    }

    &.warning {
      background-image: url('/assets/images/backgrounds/notification-background.png'), linear-gradient(0deg, #DD6A00, #DD6A00);
      box-shadow: 0 0 32px 0 rgba(255, 204, 1, 0.29);

      .notifier-navigation {
        path {
          stroke: #DD6A00;
        }
      }
    }

    .notifier-wrapper {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      align-items: center;

      gap: 15px;

      padding: 30px;
      padding-bottom: 23px;

      .notifier-content {
        flex-grow: 1;

        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        gap: 10px;

        .notifier-title {
          display: flex;
          align-items: center;
          flex-wrap: nowrap;
          gap: 10px;

          .notifier-title__text {
            @include roboto(24px, 600, rgba(255, 255, 255, 0.90));
            line-height: 25.2px;
          }

          .notifier-title__icon {
            min-width: 21px;
            min-height: 21px;
            max-width: 21px;
            max-height: 21px;
          }
        }

        .notifier-message {
          @include roboto(18px, 400, rgba(255, 255, 255, 0.90));
          line-height: 25.2px;
          word-break: normal;

          &::ng-deep b {
            @include roboto(18px, 400, rgba(255, 255, 255, 0.90));
            line-height: 25.2px;
            word-break: normal;
            font-weight: bold;
          }
        }
      }

      .notifier-navigation {
        min-width: 57px;
        min-height: 57px;
        max-width: 57px;
        max-height: 57px;

        display: flex;
        justify-content: center;
        align-items: center;

        background-color: #fff;
        border-radius: 50%;
      }
    }

    .notifier-progress {
      position: absolute;
      height: 7px;
      width: 100%;
      bottom: 0;
      left: 0;

      background: rgba(255, 255, 255, 0.46);
      backdrop-filter: blur(3px);

      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        height: 100%;
        width: 100%;
        background: #FFF;

        animation: navigation-progress-animation var(--animationTime, 5s) linear forwards;
      }
    }
  }

  @keyframes navigation-progress-animation {
    100% {
      right: 100%;
    }
  }

  @media screen and (max-width: 665px) {
    .notifier {
      width: calc(100% - 40px);
      max-width: calc(100% - 40px);

      .notifier-wrapper {
        .notifier-content {
          .notifier-title {
            @include roboto(20px, 600);
            line-height: 25.2px;
          }

          .notifier-message {
            @include roboto(16px, 400, rgba(255, 255, 255, 0.90));
            line-height: 25.2px;
          }
        }
      }

    }
  }
}
